body {
  background-color: #89c2d9;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.buttonStyle {
  width: 120px;
  height: 50px;
  border: 2px solid #77afe7;
  border-width:0px;
  align-items:'center';
  justify-content:'center';
  color: #425871;
  background: #FAC898;
  border-radius:10px;
  padding:10px;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25), 0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
}

.buttonStyle:hover{
  background-color: beige;
}

h1 {
  font-size: 3em;
  color: #04395e;
  background-clip: text;
  -webkit-background-clip: text;
  text-align: center;
  font-family: "Bebas Neue", sans-serif;
  text-shadow: -2px 0 #0a1128, 0 2px #0a1128, 2px 0 #0a1128, 0 -2px #0a1128;
}

h2{
  font-weight: 600;
  font-size: 3em;
  color: #04395e;
  background-color: #C3B1E1;
  margin: 20px;
  border-radius:20px;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25), 0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
  shadow: 10px;
}

h3{
font-weight:200;
color: #04395e;
font-size: 1.5em;
}

.textStyle{
  color:  #ed7966;
  text-decoration: none !important;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table_footer {
  width: 700px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

h4{
  font-family:'Raleway', sans-serif;
  font-size:60px;
	letter-spacing:0.05em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1.5px;
  -webkit-text-stroke-color: #04d9ff;
  text-shadow: 
						2px 2px #150a7a,
						5px 5px #000000;
}
/* Slideshow */

.slideshow {
  margin: 0 auto;
  overflow: hidden;
  /* change width  */
  max-width: 800px;
}

.slideshowSlider {
  white-space: nowrap;
  transition:  2000ms;
  /* transition: ease 2000ms; */
}

.slide {
  display: inline-block;
  /* change height */
  height: 250px;
  width: 100%;
  border-radius: 40px;
}

/* Buttons */

.slideshowDots {
  text-align: center;
}

.slideshowDot {
  display: inline-block;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  cursor: pointer;
  margin: 15px 7px 0px;

  background-color: #c4c4c4;
}

.slideshowDot.active {
  background-color: #6a0dad;
}


/* Login in */
.container{
  border-radius: 25px;
  display: flex;
  flex-direction:  column;
  margin: auto;
  background: #C3B1E1;
  padding-bottom: 30px;
  width:600px;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25), 0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
}

.header{
  display: flex;
  flex-direction:  column;
  align-items: center;
  gap: 9px;
  width: 100%;
  margin-top: 30px;
}

.text{
  color:#6a0dad;
  font-size: 48px;
  font-weight: 700;
}

.underline{
  width:61px;
  height: 6px;
  background: #553c9a;
  border-radius: 9px;
}

.inputs{
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.input{
  display: flex;
  align-items: center;
  margin: auto;
  width: 480px;
  height: 60px;
  padding:10px;
  background: #eaeaea;
  border-radius: 6px;
}

.input img{
  margin:0px 30px;
}

.input input{
  height: 50px;
  width: 400px;
  background: transparent;
  border: none;
  outline: none;
  color: #797979;
  font-size: 19px;
}

.forgot-password{
  margin-top: 2px;
  color: #797979;
  font-size:18px;
}

.forgot-password span{
  color: #4c00b4;
  cursor: pointer;
}

.submit-container{
  display: flex;
  gap:30px;
  margin: 30px auto;
}

.submit{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 40px;
  color: #fff;
  background: #4c00b4;
  border-radius: 50px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
}



.gray{
  background: #EAEAEA;
  color: #676767;
}

.gray:hover{
  background: #a6a6a6;
}

.home-container{
  display: flex;
  gap:30px;
  margin: 30px auto;
}

.home{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 30px;
  color: #425871;
  background: #FAC898;
  border-radius: 50px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
}

.home:hover{
  background-color: beige;
}

.recommendation-box {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f4f4f4;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.recommendation-item {
  display: flex;
  align-items: flex-start;
}

.album-image {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 5px;
  margin-right: 15px;
}

.song-info {
  flex-grow: 1;
}

.song-name {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
}

.artist-name {
  font-size: 14px;
  color: #666;
}

.no-data {
  text-align: center;
  color: #999;
}

.two-column-container {
  display: flex;
}

.column {
  flex: 1; /* This makes each column take up equal width */
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

